//文字コードはutf-8
@charset "UTF-8";

@use "../global" as g;

/* カード
========================================================*/
#{g.$prefix_mod}{
  &Card {
    &Badge {
      display: grid;
      gap: 13px;
      &__img {
        width: 64px;
        height: 64px;
        padding: 10px;
        border: 1px solid var(--line);
        border-radius: 15px;
        @include g.aspect_ratio(1, 1);
      }
      &__name {
        text-align: center;
        @include g.fontsize(11);
      }
    }
    &Training {
      display: grid;
      width: 280px;
      padding-top: 8px;
      border-radius: 15px;
      transition: g.$transition_default;
      @include g.media_queries{
        width: 295px;
      }
      &:hover {
        opacity: 0.6;
      }
      &::before {
        display: flex;
        justify-content: center;
        width: 100%;
        padding-block: 8px 23px;
        margin-bottom: -15px;
        border-radius: 15px 15px 0 0;
        @include g.fontsize(12);
        line-height: 1;
        color: var(--white);
      }
      &__inner {
        overflow: hidden;
        background: var(--white);
        border-radius: 15px;
        border: 1px solid var(--line);
      }
      &__img {
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
        aspect-ratio: 280 / 160;
        width: 280px;
        height: 160px;
        @include g.media_queries{
          aspect-ratio: 295 / 115;
          width: 295px;
          height: 115px;
        }
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      &__textArea {
        padding: 20px 20px 16px;
        background: var(--white);
        .title {
          font-weight: 700;
          @include g.fontsize(18);
          @include g.media_queries{
            @include g.fontsize(16);
            line-height: 2;
          }
        }
        .date {
          display: flex;
          gap: 3px;
          @include g.fontsize(10);
          color: var(--dark_gray);
        }
      }
      &--acquired {
        &::before {
          content: '修了済';
          background: var(--gray);
        }
      }
      &--inProgress {
        &::before {
          content: '受講中';
          background: #3bc5ab;
        }
      }
      &--notStarted {
        &::before {
          content: '未受講';
          background: var(--blue);
        }
      }
    }
  }
}
