//文字コードはutf-8
@charset "UTF-8";

@use "../global" as g;

* {
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
}
body {
  font-family: g.$fontface_default;
  @include g.fontsize(14);
  line-height: 1.4;
  letter-spacing: 0;
  color: g.$base_text_color;
  word-break: break-all;
  &.scrollLock{
    overflow: hidden;
  }
}
a, button {
  color: g.$base_text_color;
  @include g.media_queries(pc){
    transition: all 0.3s ease-out;
  }
}
