@charset "UTF-8";

@use "../global" as g;

/* モーダル
========================================================*/
#{g.$prefix_mod}{
  &Modal {
    opacity: 0;
    visibility: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.2);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    transition: opacity 0.3s ease, visibility 0.3s ease;
    &.is-open {
      opacity: 1;
      visibility: visible;
    }
    &__content {
      overflow-y: auto;
      max-width: 80vw;
      max-height: 80vh;
      padding: 40px;
      background: var(--white) url('/assets/img/common/img_bg_section.webp') no-repeat top center / max(1180px, 100%) auto;
      border-radius: 15px;
      position: relative;
      @include g.media_queries{
        max-width: 96vw;
        padding-inline: 20px;
      }
      &__head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 24px;
        padding-block: 8px 16px;
        border-bottom: 1px solid var(--blue);
        .modHeading {
          margin-bottom: 0;
        }
      }
      &__close {
        cursor: pointer;
        width: 30px;
        height: 30px;
        background: var(--gradient_green);
        border-radius: 50%;
        position: absolute;
        top: 18px;
        right: 18px;
        transform: rotate(45deg);
        &::before,
        &::after {
          content: "";
          width: 16px;
          height: 3px;
          background: var(--white);
          border-radius: 50em;
          position: absolute;
          top: 50%;
          left: 50%;
        }
        &::before {
          transform: translate(-50%, -50%);
        }
        &::after {
          transform: translate(-50%, -50%) rotate(90deg);
        }
        &:hover {
          background-color: #f0f0f0;
          color: #333;
        }
      }
    }
  }
}
