@charset "UTF-8";

@use "../global" as g;

/* レイアウト
========================================================*/
.layout{
  display: flex;
  min-height: 100vh;
  &:has(.sidebar.is-close),
  &:has(.sidebar.is-closed) {
    .layout {
      &__main {
        width: calc(100% - 75px);
      }
    }
  }
  &__main{
    width: calc(100% - 180px);
    padding: 20px 40px;
    transition: width g.$transition_speed_sidebar ease-out;
    @include g.media_queries {
      width: 100%;
      padding: 15px;
    }
  }
}
