//文字コードはutf-8
@charset "UTF-8";

@use "../global" as g;

/* サイドメニュー
========================================================*/
.sidebar {
  overflow-x: hidden;
  width: 180px;
  background: var(--white);
  position: relative;
  transition: g.$transition_speed_sidebar ease-out;
  z-index: 1;
  &.is-close {
    width: 75px;
    .sidebar {
      &__inner {
        width: 75px;
      }
      &__header {
        &__logo {
          .text {
            opacity: 0;
          }
        }
      }
      &__nav {
        &__list {
          overflow: visible;
          .item {
            &__title {
              &::after {
                opacity: 0;
              }
              .text {
                opacity: 0;
              }
            }
          }
        }
        &__subList {
          .item {
            > a {
              .text {
                opacity: 0;
              }
            }
          }
        }
      }
      &__btn {
        right: 22.5px;
        &::before {
          transform: scale(1, -1);
        }
      }
    }
  }
  &.is-closed {
    .sidebar {
      &__inner {
        width: 75px;
        overflow: visible;
      }
      &__header {
        &__logo {
          .text {
            display: none;
          }
        }
      }
      &__nav {
        &__list {
          .item {
            max-width: 75px;
            padding-inline: 16.5px;
            position: relative;
            &__title {
              &::before {
                content: '';
                display: block;
                width: 20px;
                height: 50px;
                position: absolute;
                right: 0;
              }
              &:hover {
                @include g.media_queries(pc){
                  +.item__childMenu {
                    opacity: 1;
                  }
                }
              }
              &:has(+ .item__childMenu:hover) {
                @include g.media_queries(pc){
                  background: var(--gradient_green);
                  -webkit-background-clip: text;
                  -webkit-text-fill-color: transparent;
                  background-clip: text;
                  color: transparent;
                  .icon {
                    background: var(--gradient_green);
                  }
                }
              }
              &::after {
                display: none;
              }
              .text {
                display: none;
              }
            }
            &__childMenu {
              opacity: 0;
              display: block !important;
              width: 170px;
              padding: 15px;
              background: var(--white);
              border-radius: 8px;
              box-shadow: 0 15px 15px rgba(0, 0, 0, 0.1);
              position: absolute;
              top: 0;
              left: calc(100% - 11px);
              z-index: 99;
              &:hover {
                @include g.media_queries(pc){
                  opacity: 1;
                }
              }
              .item {
                width: 100%;
                max-width: initial;
                padding: 0;
                border-radius: 0;
                >a {
                  padding: 8px 10px;
                  border-radius: 0;
                }
              }
            }
          }
        }
        &__subList {
          width: 75px;
          .item {
            padding-inline: 16.5px;
            > a {
              .text {
                display: none;
              }
            }
          }
        }
      }
    }
  }
  &.is-open {
    .sidebar {
      &__header {
        &__logo {
          .text {
            opacity: 0;
          }
        }
      }
      &__nav {
        &__list {
          .item {
            &__title {
              .text {
                white-space: nowrap;
                opacity: 0;
              }
            }
          }
        }
        &__subList {
          .item {
            > a {
              .text {
                white-space: nowrap;
                opacity: 0;
              }
            }
          }
        }
      }
    }
  }
  &__inner {
    overflow-x: hidden;
    width: 180px;
    height: 100%;
    max-height: 100vh;
    position: fixed;
    transition: g.$transition_speed_sidebar ease-out;
  }
  &__header {
    width: 180px;
    padding: 10px 20px;
    &__logo {
      display: flex;
      position: relative;
      .text {
        transition: opacity g.$transition_default;
      }
    }
  }
  &__nav {
    width: 180px;
    display: grid;
    align-content: space-between;
    &__list {
      .item {
        position: relative;
        padding: 4px 6px;
        transition: g.$transition_default;
        &.is-active {
          .item__title {
            .icon {
              background: var(--gradient_green);
            }
            .text {
              background: var(--gradient_green);
              -webkit-background-clip: text;
              -webkit-text-fill-color: transparent;
              background-clip: text;
              color: transparent;
            }
          }
        }
        &__title {
          cursor: pointer;
          display: flex;
          align-items: center;
          gap: 10px;
          padding: 8.5px 9px;
          transition: g.$transition_default;
          &.jsSidebarItemTitle {
            &::after {
              content: "";
              display: inline-block;
              width: 16px;
              height: 16px;
              margin-left: auto;
              background: url("/assets/img/common/icon_arrow.svg") no-repeat center center;
              background-size: contain;
              transform: scale(1, -1);
              transition: g.$transition_default;
            }
            &.is-open {
              &::after {
                transform: scale(1, 1);
              }
            }
          }
          &:hover {
            background: var(--light_blue);
            border-radius: 100px;
            .icon {
              background: var(--gradient_green);
            }
            .text {
              background: var(--gradient_green);
              -webkit-background-clip: text;
              -webkit-text-fill-color: transparent;
              background-clip: text;
              color: transparent;
            }
          }
          .icon {
            display: inline-block;
            width: 25px;
            height: 25px;
            aspect-ratio: 1;
            background: var(--black);
            mask-repeat: no-repeat;
            mask-position: center;
            mask-size: contain;
            &--dashboard {
              mask-image: url("/assets/img/common/sidebar/icon_dashboard.svg");
            }
            &--selfing {
              mask-image: url("/assets/img/common/sidebar/icon_selfing.svg");
            }
            &--study {
              mask-image: url("/assets/img/common/sidebar/icon_study.svg");
            }
            &--1on1 {
              mask-image: url("/assets/img/common/sidebar/icon_1on1.svg");
            }
            &--timeline {
              mask-image: url("/assets/img/common/sidebar/icon_timeline.svg");
            }
          }
          .text {
            font-weight: bold;
            transition:  g.$transition_default;
          }
        }
        &__childMenu {
          display: none;
          padding: 0 20px 0 30px;
          .item {
            padding: 0;
            &:not(:first-child) {
              margin-top: 2px;
            }
            > a {
              display: block;
              border-radius: 40px;
              color: #777;
              min-width: 118px;
              padding: 6px 12px;
              font-size: 13px;
              &:hover {
                @include g.media_queries(pc){
                  background: var(--light_blue);
                  color: #0c9fc3;
                }
              }
            }
          }
        }
      }
    }
    &__subList {
      width: 180px;
      .item {
        padding: 4px 6px;
        transition: g.$transition_default;
        > a {
          display: flex;
          align-items: center;
          gap: 10px;
          padding: 8.5px 9px;
          &:hover {
            @include g.media_queries(pc){
              background: var(--light_blue);
              border-radius: 100px;
              .icon {
                &:not(.icon--user) {
                  background: var(--gradient_green);
                }
              }
              .text {
                background: var(--gradient_green);
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
                background-clip: text;
                color: transparent;
              }
            }
          }
          .icon {
            display: inline-block;
            width: 25px;
            height: 25px;
            aspect-ratio: 1;
            background: #999;
            mask-repeat: no-repeat;
            mask-position: center;
            mask-size: contain;
            &--help {
              mask-image: url("/assets/img/common/sidebar/icon_help.svg");
            }
            &--setting {
              mask-image: url("/assets/img/common/sidebar/icon_setting.svg");
            }
            &--user {
              border-radius: 50%;
              background: transparent;
            }
          }
          .text {
            color: #999;
            transition: g.$transition_default;
          }
        }
      }
    }
  }
  &__btn {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    aspect-ratio: 1;
    background: var(--light_gray);
    border-radius: 50%;
    position: absolute;
    right: 25px;
    bottom: 21px;
    transition: g.$transition_speed_sidebar ease-out;
    transform: rotate(-90deg);
    &::before {
      content: "";
      display: block;
      width: 20px;
      height: 20px;
      aspect-ratio: 1;
      background: var(--gradient_green);
      mask-image: url("/assets/img/common/icon_arrow.svg"); mask-repeat: no-repeat; mask-position: center; mask-size: contain;
      transition: g.$transition_default;
    }
  }
  &--user {
    @include g.media_queries{
      overflow: visible;
      width: 100%;
      height: 60px;
      position: fixed;
      bottom: 0;
      left: 0;
      z-index: 999;
    }
    .sidebar {
      &__inner {
        @include g.media_queries{
          overflow: visible;
          width: 100%;
          position: relative;
        }
      }
      &__header {
        @include g.media_queries{
          display: none;
        }
      }
      &__nav {
        height: calc(100% - 146px);
        @include g.media_queries{
          overflow: hidden auto;
          width: 100%;
          height: 70vh;
          padding: 0 21px;
          background: var(--white);
          border-radius: 15px 15px 0 0;
          box-shadow: 0 -20px 40px rgba(0, 0, 0, 0.05);
          transition: g.$transition_default;
          position: absolute;
          top: 30vh;
        }
        &.is-open {
          @include g.media_queries{
            top: -70vh;
          }
        }
        &__list {
          @include g.media_queries{
            margin-block: 25px;
            .item {
              padding: 0;
              &--dashboard,
              &--study {
                display: none;
              }
              &__title {
                padding: 10px 15px;
                .icon {
                  width: 30px;
                  height: 30px;
                }
                &.jsSidebarItemTitle {
                  &::after {
                    width: 30px;
                    height: 30px;
                    background: url("/assets/img/common/sidebar/icon_sp_arrow.svg") no-repeat center center;
                    background-size: contain;
                  }
                }
                &:hover,
                &.is-open {
                  background: var(--light_blue);
                  border-radius: 8px;
                  .icon {
                    background: var(--gradient_green);
                  }
                  .text {
                    background: var(--gradient_green);
                    -webkit-background-clip: text;
                    -webkit-text-fill-color: transparent;
                    background-clip: text;
                    color: transparent;
                  }
                }
              }
              &__childMenu {
                padding: 0;
                .item {
                  > a {
                    padding: 15px 55px;
                  }
                }
              }
            }
          }
        }
        &__subList {
          @include g.media_queries{
            width: 100%;
            margin-block: auto 25px;
            .item {
              > a {
                &:hover {
                  background: var(--light_blue);
                  border-radius: 8px;
                  .icon {
                    background: var(--gradient_green);
                  }
                  .text {
                    background: var(--gradient_green);
                    -webkit-background-clip: text;
                    -webkit-text-fill-color: transparent;
                    background-clip: text;
                    color: transparent;
                  }
                }
                .icon {
                  width: 30px;
                  height: 30px;
                }
              }
            }
          }
        }
      }
      &__mobileTab {
        @include g.media_queries(pc){
          display: none;
        }
        @include g.media_queries{
          height: 100%;
          background: var(--white);
          border-top: 1px solid var(--light_gray);
          position: relative;
        }
        &__list {
          @include g.media_queries{
            display: flex;
            align-items: center;
            justify-content: center;
            height: 100%;
            .item {
              width: 20%;
              padding-block: 10px;
              position: relative;
              @media screen and (max-width: 350px){
                width: 22%;
              }
              &::before {
                opacity: 0;
                content: "";
                display: block;
                width: 100%;
                height: 3px;
                background: var(--gradient_green);
                position: absolute;
                top: 0;
                left: 0;
                transition: g.$transition_default;
              }
              &.is-active,
              &.is-open {
                &::before {
                  opacity: 1;
                }
                .item__title {
                  .icon {
                    background: var(--gradient_green);
                    &--menu {
                      background: transparent;
                      span {
                        background: transparent;
                        &::before {
                          background: var(--gradient_green);
                          transform: rotate(-45deg);
                          top: 0;
                        }
                        &::after {
                          background: var(--gradient_green);
                          transform: rotate(45deg);
                          top: 0;
                        }
                      }
                    }
                  }
                  .text {
                    background: var(--gradient_green);
                    -webkit-background-clip: text;
                    -webkit-text-fill-color: transparent;
                    background-clip: text;
                    color: transparent;
                  }
                }
              }
              &--study {
                @media screen and (max-width: 350px){
                  width: 60px;
                }
              }
              &--menu {
                max-width: calc(60 / 375 * 100%);
                @media screen and (max-width: 350px){
                  width: 40px;
                }
              }
              &__title {
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 4px;
                width: 100%;
                padding: 0;
                .icon {
                  display: inline-block;
                  width: 24px;
                  height: 24px;
                  aspect-ratio: 1;
                  background: var(--black);
                  mask-repeat: no-repeat;
                  mask-position: center;
                  mask-size: contain;
                  transition: g.$transition_default;
                  &--dashboard {
                    mask-image: url("/assets/img/common/sidebar/icon_dashboard.svg");
                  }
                  &--skillmap {
                    mask-image: url("/assets/img/common/sidebar/icon_skillmap.svg");
                  }
                  &--contents {
                    mask-image: url("/assets/img/common/sidebar/icon_contents.svg");
                  }
                  &--study {
                    mask-image: url("/assets/img/common/sidebar/icon_study.svg");
                  }
                  &--menu {
                    background: transparent;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    span {
                      display: inline-block;
                      width: 20px;
                      height: 2px;
                      position: relative;
                      background: var(--black);
                      border-radius: 50em;
                      &::before, &::after {
                        content: "";
                        display: block;
                        width: 100%;
                        height: 2px;
                        background: var(--black);
                        position: absolute;
                        left: 0;
                        transform-origin: center;
                        border-radius: 50em;
                        transition: all 0.3s ease-out;
                      }
                      &::before {
                        top: 7px;
                      }
                      &::after {
                        top: -7px;
                      }
                    }
                  }
                }
                .text {
                  font-weight: 700;
                  @include g.fontsize(10);
                  transition: g.$transition_default;
                }
              }
            }
          }
        }
      }
      &__btn {
        @include g.media_queries{
          display: none;
        }
      }
    }
  }
  &--admin {
    .sidebar {
      &__nav {
        height: calc(100% - 76px);
        &__list {
          .item {
            &__childMenu {
              padding-left: 0;
            }
          }
        }
        &__subList {
          margin-bottom: 10px;
        }
      }
    }
  }
}
