//文字コードはutf-8
@charset "UTF-8";

@use "../global" as g;

/* リスト
========================================================*/
#{g.$prefix_mod}{
  &List {
    &Badge {
      // overflow-x: auto;
      display: flex;
      gap: 20px;
      padding-right: 20px;
      width: fit-content;
    }
    &Card {
      // overflow-x: auto;
      display: flex;
      gap: 32px;
      padding-bottom: 2px;
      @include g.media_queries{
        gap: 15px;
      }
    }
    &Ul {
      &__item {
        display: flex;
        align-items: start;
        margin-bottom: 10px;
        @include g.media_queries{
          margin-bottom: 8px;
        }
        &::before {
          content: '';
          display: inline-block;
          width: 6px;
          height: 6px;
          aspect-ratio: 1;
          margin: 7px 10px 0 0;
          border-radius: 50%;
          background: var(--black);
        }
        &:last-child {
          margin-bottom: 0;
        }
        &__inner {
          text-decoration: underline;
          text-underline-offset: 4px;
          transition: g.$transition_default;
          text-align: left;
          &:hover {
            @include g.media_queries(pc){
              color: var(--blue);
            }
          }
        }
        .acquired {
          &::before {
            content: '取得済';
            display: inline-block;
            margin-left: 10px;
            padding: 4px 16px;
            background: #3bc5ab;
            border-radius: 16px;
            @include g.fontsize(12);
            line-height: 1;
            color: var(--white);
            white-space: nowrap;
          }
        }
      }
      &--2col {
        @include g.media_queries(pc){
          display: flex;
          flex-wrap: wrap;
          gap: 20px;
        }
        .modListUl {
          &__item {
            @include g.media_queries(pc){
              width: calc(50% - 10px);
              margin-bottom: 0;
            }
          }
        }
      }
    }
  }
}
