//文字コードはutf-8
@charset "UTF-8";

@use "../../global" as g;

.user {
  &--skillmap {
    #{g.$prefix_mod} {
      &Section {
        &--qualifications {
          max-height: 100vh;
          #{g.$prefix_mod} {
            &Section {
              &__inner {
                height: calc(100% - 62px);
              }
            }
          }
        }
        &--trainindList {
          height: 100vh;
          #{g.$prefix_mod} {
            &Section {
              &__inner {
                height: calc(100% - 62px);
              }
              &__content {
                margin-bottom: 40px;
              }
            }
          }
          &:not(:has(.modSection__content:nth-child(2))) {
            height: auto;
          }
        }
      }
    }
    .layout {
      &__main {
        padding-top: 0;
      }
    }
    .skillmap {
      &Main {
        margin-top: 20px;
        &__slider {
          margin-bottom: 17px;
          padding-bottom: 2px;
          &__container {
            display: flex;
            gap: 10px;
          }
          &__btn {
            display: flex;
            border: 1px solid var(--line);
            padding: 16px;
            border-radius: 14px 16px;
            align-items: center;
            justify-content: space-between;
            gap: 6px;
            flex-shrink: 0;
            font-weight: 700;
            line-height: 1.4;
            transition: all 0.3s ease-out;
            @include g.media_queries {
              flex: 1;
              min-width: 164px;
              height: 62px;
              padding-block: 5px;
              overflow: hidden;
            }
            .name {
              text-align: left;
              transition: color 0.3s ease-out;
              @include g.fontsize(12);
            }
            .progress {
              line-height: 1;
              flex-shrink: 0;
              display: flex;
              align-items: center;
              gap: 4px;
              span {
                @include g.fontsize(18);
                color: var(--violet);
              }
            }
            &.isActive {
              box-shadow: none;
              background: var(--light_blue);
              border-color: var(--light_blue);
              pointer-events: none;
              .name {
                color: var(--blue);
              }
            }
            @include g.media_queries(pc) {
              &:hover {
                box-shadow: none;
                background: var(--light_blue);
                border-color: var(--light_blue);
                .name {
                  color: var(--blue);
                }
              }
            }
          }
        }
        &__content {
          box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.08);
          padding: 22px 30px;
          border-radius: 16px;
          background: var(--white);
          margin: 0 auto;
          margin-bottom: 20px;
          @include g.media_queries(sp_x_tablet) {
            width: 100%;
            padding: 0;
            box-shadow: none;
          }
          &__inner {
            display: none;
          }
          .head {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 12px;
            @include g.media_queries(sp_x_tablet) {
              flex-direction: column-reverse;
              align-items: flex-start;
              gap: 10px;
            }
            &__title {
              display: flex;
              align-items: center;
              @include g.fontsize(20);
              font-weight: 700;
              gap: 10px;
              h3 {
                font-weight: 700;
              }
              &__progress {
                @include g.fontsize(12);
                .title {
                  font-weight: 400;
                  color: var(--dark_gray);
                }
                .count {
                  @include g.fontsize(22);
                  color: var(--violet);
                }
              }
            }
            &__info {
              display: flex;
              align-items: center;
              gap: 14px;
              &__manual {
                display: flex;
                gap: 10px;
                align-items: center;
                .item {
                  display: flex;
                  align-items: center;
                  gap: 5px;
                  .icon {
                    max-width: 16px;
                    img {
                      width: 100%;
                      height: 100%;
                      object-fit: contain;
                      display: block;
                    }
                  }
                  .text {
                    flex-shrink: 0;
                  }
                }
              }
              &__chartBtn {
                background: var(--light_blue);
                color: var(--blue);
                padding: 4px 10px;
                border-radius: 50em;
                pointer-events: none;
                cursor: pointer;
                opacity: 0.5;
                transition: all 0.4s ease-out;
                @include g.media_queries(sp_x_tablet) {
                  display: none;
                }
              }
            }
          }
          .main {
            display: flex;
            gap: 10px;
            @include g.media_queries(sp_x_tablet) {
              flex-direction: column;
            }
            & > * {
              flex: 1;
              height: calc(100vh - 380px);
              min-height: 300px;
              width: 50%;
              @include g.media_queries(sp_x_tablet) {
                height: unset;
                min-height: unset;
                width: 100%;
              
              }
            }
            &__info {
              overflow: auto;
              .accordion {
                &__item {
                  cursor: pointer;
                  + .accordion__subItem {
                    margin-top: 11px;
                  }
                  &__wrapper {
                    padding: 13px 20px;
                    border: 1px solid var(--line);
                    border-radius: 8px;
                    background: var(--white);
                    @include g.media_queries(sp_x_tablet) {
                      padding: 12px 12px;
                    }
                    &:not(:last-child) {
                      margin-bottom: 8px;
                    }
                    &:has(> .accordion__item.isOpen) {
                      background: var(--accordionBlue);
                      .accordion__item__wrapper {
                        background: var(--blueBg);
                      }
                    }

                  }
                  &__content {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    gap: 12px;
                    .accordionTrigger {
                      display: inline-block;
                      width: 11px;
                      height: 11px;
                      margin-left: auto;
                      background: url(/assets/img/common/icon_arrow02.svg)
                        no-repeat center center;
                      background-size: contain;
                      transform: scale(1, -1);
                      transition: 0.3s ease-out;
                      display: none;
                      &.isActive {
                        transform: scale(1, 1);
                      }
                    }
                    .info {
                      flex: 1;
                      display: flex;
                      align-items: center;
                      gap: 8px;
                      .category {
                        @include g.fontsize(11);
                        padding: 2px 5px;
                        background: var(--grayBg);
                        border-radius: 4px;
                      }
                      p {
                        flex: 1;
                        font-weight: 700;
                        @include g.fontsize(12);
                        line-height: 1.4;
                        display: flex;
                        align-items: center;
                        &::after {
                          content: "";
                          display: inline-block;
                          width: 16px;
                          height: 16px;
                          background: url(/assets/img/common/icon__arrowGradient.svg)
                            no-repeat center center;
                          background-size: contain;
                          border-radius: 50%;
                          flex-shrink: 0;
                        }
                      }
                    }
                    .status {
                      display: flex;
                      align-items: center;
                      gap: 4px;
                      > * {
                        min-width: 16px;
                      }
                      .progress {
                        display: flex;
                        align-items: center;
                        @include g.fontsize(14);
                        @include g.media_queries(sp_x_tablet) {
                          // display: none;
                          @include g.fontsize(12);
                        }
                        span {
                          color: var(--violet);
                          font-weight: 700;
                          @include g.fontsize(18);
                          @include g.media_queries {
                            @include g.fontsize(14);
                          }
                        }
                        &--complete {
                          aspect-ratio: 1;
                          &::before {
                            content: "";
                            width: 16px;
                            aspect-ratio: 1;
                            background: url("#{g.$dir_img}common/icons/icon_trophy.svg")
                              no-repeat center center / contain;
                          }
                        }
                        &--hidden {
                          opacity: 0;
                        }
                      }
                      .badge {
                        display: flex;
                        align-items: center;
                        aspect-ratio: 1;
                        &::before {
                          content: "";
                          width: 16px;
                          aspect-ratio: 1;
                        }
                        &--inProgress {
                          &::before {
                            background: url("#{g.$dir_img}common/icons/icon_badgePlain.svg")
                              no-repeat center center / contain;
                          }
                        }
                        &--complete {
                          &::before {
                            background: url("#{g.$dir_img}common/icons/icon_badgeClear.svg")
                              no-repeat center center / contain;
                          }
                        }
                        &--hidden {
                          opacity: 0;
                        }
                      }
                      .bookmark {
                        display: flex;
                        align-items: center;
                        aspect-ratio: 1;
                        &::before {
                          content: "";
                          width: 16px;
                          aspect-ratio: 1;
                          background: url("#{g.$dir_img}common/icons/icon_bookmark.svg")
                            no-repeat center center / contain;
                        }
                        &--hidden {
                          opacity: 0;
                        }
                      }
                    }
                  }
                  &:has(+ .jsAccordionitem) {
                    .accordionTrigger {
                      display: block;
                    }
                  }
                }
                &__subItem {
                  &.jsAccordionitem {
                    display: none;
                  }
                }
                & > .accordion__item__wrapper {
                  padding-right: 30px;
                  @include g.media_queries(sp_x_tablet) {
                    padding-right: 12px;
                  }
                }
              }
              .simplebar-track {
                right: 8px;
              }
            }
            &__detail {
              border-radius: 15px;
              border: none;
              &:has(.accordionDetail__wrapper:not(:empty)) {
                border: 1px solid var(--line);
                overflow: hidden;
              }
              &__inner {
                overflow: auto;
                height: calc(100vh - 380px);
                min-height: 300px;
                @include g.media_queries(sp_x_tablet) {
                  height: auto;
                }
              
              }
              #{g.$prefix_mod} {
                &Chart {
                  width: 100%;
                  height: 100%;
                  position: relative;
                  overflow: auto;
                  padding: 22px;
                  min-height: calc(100vh - 380px);
                  @include g.media_queries(sp_x_tablet) {
                    min-height: unset;
                  }
                  &__inner {
                    display: flex;
                    justify-content: space-between;
                    flex-direction: column;
                    height: 100%;
                    @include g.media_queries(sp_x_tablet) {
                      min-width: 490px;
                    }
                  }
                  &__legend {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    gap: 28.56px;
                    margin-bottom: 25px;
                    flex-wrap: wrap;
                    @include g.fontsize(12);
                    color: var(--white);
                    &__item {
                      display: flex;
                      align-items: center;
                      gap: 10px;
                      &:nth-child(1) {
                        .chartRepresent {
                          display: block;
                          width: 20px;
                          height: 20px;
                          background: var(--green);
                        }
                      }
                      &:nth-child(2) {
                        .chartRepresent {
                          display: block;
                          width: 20px;
                          height: 3px;
                          background: var(--white);
                        }
                      }
                    }
                  }
                  canvas {
                    margin-top: auto;
                    width: 100% !important;
                    height: auto !important;
                    aspect-ratio: 442 / 301;
                    max-width: 442px;
                    margin-inline: auto;
                    margin-bottom: 28px;
                  }
                  &__description {
                    color: var(--white);
                    max-width: 680px;
                    width: 100%;
                    margin-inline: auto;
                    border: 1px solid var(--white);
                    margin-bottom: 22px;
                    padding: 15px;
                    border-radius: 10px;
                    @include g.fontsize(12);
                    &__title {
                      font-weight: 700;
                      margin-bottom: 12px;
                    }
                    &__list {
                      counter-reset: chartList;
                      li {
                        counter-increment: chartList;
                        display: flex;
                        align-items: flex-start;
                        gap: 6px;
                        &:not(:last-child) {
                          margin-bottom: 8px;
                        }
                        &::before {
                          content: counter(chartList);
                          border: 1px solid var(--white);
                          border-radius: 50em;
                          font-size: 10px;
                          width: 16px;
                          height: auto;
                          aspect-ratio: 1;
                          display: flex;
                          align-items: center;
                          justify-content: center;
                          flex-shrink: 0;
                        }
                      }
                    }
                  }
                }
              }
              &.isShowingDetail {
                #{g.$prefix_mod} {
                  &Chart {
                    display: none;
                  }
                }
                .jsDetailItem {
                  display: block;
                }
              }
            }
          }
          .accordionDetail {
            border-radius: 15px;
            padding: 15px 20px;
            padding-top: 0;
            overflow: unset;
            background: var(--accordionBlue);
            @include g.media_queries(sp_x_tablet) {
              max-height: none;
              padding: 0;
              border-radius: 0;
            }
            &__header {
              display: flex;
              padding-bottom: 14px;
              align-items: center;
              justify-content: space-between;
              border-bottom: 2px solid var(--blue);
              margin-bottom: 10px;
              position: sticky;
              padding-top: 15px;
              gap: 10px;
              top: 0;
              background: var(--accordionBlue);
              @include g.media_queries {
                flex-wrap: wrap;
              }
              &__info {
                display: flex;
                align-items: center;
                gap: 8px;
                @include g.media_queries {
                  display: contents;
                }
                .title {
                  @include g.fontsize(16);
                  font-weight: 700;
                  flex: 1;
                  @include g.media_queries {
                    width: 100%;
                    flex: unset;
                    @include g.fontsize(12);
                  }
                }
                .badge {
                  display: flex;
                  align-items: center;
                  gap: 4px;
                  &__logo {
                    #{g.$prefix_mod} {
                      &CardBadge__img {
                        width: 32px;
                        height: 32px;
                        padding: 0;
                        box-shadow: none;
                        border: none;
                        @include g.aspect_ratio(1, 1);
                        @include g.media_queries(sp_x_tablet) {
                          width: 27px;
                          height: 27px;
                        }
                      }
                    }
                  }
                  &__progress {
                    @include g.fontsize(12);
                    flex-shrink: 0;
                    @include g.media_queries {
                      @include g.fontsize(10);
                    }
                    span {
                      color: var(--violet);
                    }
                  }
                }
              }
              &__progress {
                @include g.fontsize(12);
                font-weight: 700;
                flex-shrink: 0;
                @include g.media_queries {
                  @include g.fontsize(10);
                }
                .count {
                  @include g.fontsize(24);
                  color: var(--violet);
                  margin-inline: 8px 4px;
                  line-height: 1;
                  @include g.media_queries(sp_x_tablet) {
                    @include g.fontsize(17);
                    margin-inline-end: 4px;
                  }
                }
              }
            }
            &__section {
              &__container {
                overflow: auto;
                // height: calc(390px - 88px);
                @include g.media_queries(sp_x_tablet) {
                  height: fit-content;
                }
              }
              &:not(:last-child) {
                margin-bottom: 20px;
              }
              &__header {
                display: flex;
                padding-block: 8px;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 4px;
                @include g.media_queries {
                  @include g.fontsize(12);
                }
                .info {
                  display: flex;
                  align-items: center;
                  gap: 8px;
                  &__title {
                    font-weight: 700;
                  }
                  .badge {
                    display: flex;
                    align-items: center;
                    gap: 4px;
                    &__logo {
                      #{g.$prefix_mod} {
                        &CardBadge__img {
                          width: 25px;
                          height: 25px;
                          padding: 0;
                          box-shadow: none;
                          border: none;
                          @include g.aspect_ratio(1, 1);
                          @include g.media_queries {
                            width: 20px;
                            height: 20px;
                          }
                        }
                      }
                    }
                    &__progress {
                      @include g.fontsize(12);
                      span {
                        color: var(--violet);
                      }
                    }
                  }
                }
                .progress {
                  @include g.fontsize(12);
                  font-weight: 700;
                  .count {
                    @include g.fontsize(24);
                    color: var(--violet);
                    margin-inline: 8px 4px;
                    line-height: 1;
                    @include g.media_queries(sp_x_tablet) {
                      @include g.fontsize(14);
                    }
                  }
                }
              }
              &__content {
                .course {
                  display: flex;
                  align-items: center;
                  justify-content: space-between;
                  gap: 12px;
                  padding: 8px 0;
                  &:not(:last-child) {
                    border-bottom: 1px solid var(--line);
                    padding: 8px 0;
                  }
                  &__img {
                    border-radius: 8px;
                    flex-basis: 89px;
                    flex-shrink: 0;
                    @include g.aspect_ratio(89, 50);
                    @include g.media_queries(sp_x_tablet) {
                      flex-basis: 76px;
                      @include g.aspect_ratio(1, 1);
                    }
                  }
                  &__info {
                    display: flex;
                    align-items: center;
                    gap: 10px;
                    flex: 1;
                    @include g.media_queries(sp_x_tablet) {
                      flex-wrap: wrap;
                      justify-content: space-between;
                    }
                    &__category {
                      @include g.fontsize(10);
                      font-weight: 700;
                      background: var(--white);
                      flex-shrink: 0;
                      padding: 5px;
                      line-height: 1;
                      &--blue {
                        color: var(--blue);
                      }
                      &--green {
                        color: var(--darkGreen);
                      }
                      &--orange {
                        color: var(--orange);
                      }
                    }
                    &__title {
                      @include g.fontsize(12);
                      line-height: 1.4;
                      display: -webkit-box;
                      -webkit-box-orient: vertical;
                      -webkit-line-clamp: 2;
                      overflow: hidden;
                      flex: 1;
                      @include g.media_queries(sp_x_tablet) {
                        width: 100%;
                        order: 3;
                        flex: unset;
                        -webkit-line-clamp: 3;
                      }
                    }
                    &__btn {
                      font-weight: 700;
                      white-space: nowrap;
                      background: var(--blue);
                      color: var(--white);
                      padding: 3px 5px;
                      border-radius: 50em;
                      border: 1px solid var(--blue);
                      width: 114px;
                      flex-shrink: 0;
                      transition: all 0.3s ease-out;
                      @include g.media_queries {
                        @include g.fontsize(10);
                        width: 99px;
                      }
                      &:hover {
                        background: var(--white);
                        color: var(--blue);
                      }
                      &--continue {
                        background: var(--white);
                        color: var(--blue);
                        &:hover {
                          background: var(--blue);
                          color: var(--white);
                        }
                      }
                    }
                  }
                }
              }
            }
          }
          .jsDetailItem {
            display: none;
          }
          &:has(.isShowingDetail) {
            .head {
              &__info {
                &__chartBtn {
                  background: var(--gradient_green);
                  color: var(--white);
                  padding: 4px 10px;
                  border-radius: 50em;
                  pointer-events: auto;
                  opacity: 1;
                  &:hover {
                    background: var(--light_blue);
                    color: var(--blue);
                  }
                }
              }
            }
          }
        }
        &__badge {
          padding: 10px 0 10px 40px;
          background: none;
          display: flex;
          align-items: center;
          box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.08);
          border-radius: 15px;
          @include g.media_queries(sp_x_tablet) {
            flex-direction: column;
            align-items: flex-start;
            padding-left: 20px;
          }
          &__head {
            flex-shrink: 0;
            flex-basis: 188px;
            @include g.fontsize(12);
            @include g.media_queries(sp_x_tablet) {
              flex-basis: unset;
              margin-bottom: 20px;
            }
            .title {
              @include g.fontsize(20);
              font-weight: 700;
              margin-bottom: 6px;
            }
            .achievement {
              font-weight: 700;
              strong {
                @include g.fontsize(27);
                font-weight: 700;
                color: var(--violet);
              }
            }
          }
          &__content {
            width: calc(100% - 188px);
            // padding-right: 20px;
            @include g.media_queries(sp_x_tablet) {
              width: 100%;
            }
          }
        }
      }
      &Qualification {
        display: flex;
        gap: 64px;
        @include g.media_queries(sp_x_tablet) {
          gap: 40px;
        }
        @include g.media_queries {
          flex-direction: column;
          align-items: center;
          gap: 40px;
        }
        &__left {
          @include g.media_queries {
            text-align: center;
            width: 100%;
          }
          .img {
            > img {
              @include g.media_queries {
                width: 227px;
                height: 227px;
              }
            }
          }
          .modBtn {
            margin: 20px 5px 0;
            padding-block: 8px;
            @include g.media_queries {
              width: 100%;
              margin-top: 40px;
            }
          }
        }
        &__right {
          width: 100%;
          .modListUl {
            &__item {
              @include g.media_queries(pc_x_tablet) {
                max-width: 460px;
              }
              @include g.media_queries(sp_x_tablet) {
                width: 100%;
              }
            }
          }
        }
        &Detail {
          display: flex;
          flex-wrap: wrap;
          max-width: 569px;
          min-width: 460px;
          border: 1px solid var(--gray);
          &__wrap {
            overflow-x: auto;
            width: 100%;
          }
          dt, dd {
            &:not(:last-child) {
              border-bottom: 1px solid var(--gray);
            }
          }
          dt {
            width: 135px;
            padding: 15px;
            background: var(--light_blue);
            border-right: 1px solid var(--gray);
            font-weight: 700;
          }
          dd {
            width: calc(100% - 135px);
            padding: 15px 15px 15px 10px;
            a {
              text-decoration: underline;
              text-underline-offset: 4px;
              transition: g.$transition_default;
              &:hover {
                @include g.media_queries(pc) {
                  color: var(--blue);
                }
              }
            }
          }
        }
      }
      &Schedule {
        &__date {
          font-weight: 700;
          margin-bottom: 10px;
        }
        &__text {
          margin-bottom: 21px;
          > * {
            &:not(:last-child) {
              margin-bottom: 10px;
            }
          }
          time {
            display: inline-block;
          }
        }
      }
    }
  }
}
