//文字コードはutf-8
@charset "UTF-8";

// 使用変数
//===================================================================

// 画像ディレクトリ
$dir_img: "/assets/img/";

// ブレークポイント
$bp_pc_min: 769px;
$bp_tablet_max: 1000px;
$bp_tablet_min: 1001px;
$bp_smartphone_max: 768px;

// font
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700;900&family=Oswald:wght@700&display=swap');
$fontface_default: 'Noto Sans JP', sans-serif;
$fontface_en: 'Oswald', sans-serif;


// テキストカラー
$base_text_color: var(--black);
$strong_text_color: var(--violet);


//コンテンツ幅
$base_content_width: 1200px;


//class名の接頭辞
$prefix_mod: ".mod";

//トランジション
$transition_default: 0.3s ease-out;
$transition_speed_sidebar: 0.5s;


//カラー
:root{
  --white: #fff;
  --black: #333;
  --mid_gray: #f0f0f0;
  --light_gray: #f8f8f8;
  --gray: #dcdcdc;
  --dark_gray: #777;
  --light_blue: #e7f7f7;
  --blue: #17a4c4;
  --violet: #9c50c1;
  --gradient_green: linear-gradient(to right, #0ED191 0%, #0EAAD1 100%);
  --line: #D6D6D6;
  --blueBg: #F8FFFF;
  --green: #C8E390;
  --darkGreen: #10BB94;
  --orange: #FDA700;
  --grayBg: #EEE;
  --accordionBlue: #E4FFFF;
}