//文字コードはutf-8
@charset "UTF-8";

@use "../global" as g;

/* 汎用パーツ
========================================================*/
#{g.$prefix_mod}{
  &Section {
    margin: auto;
    padding: 40px;
    margin-block: 40px;
    background: url('/assets/img/common/img_bg_section.webp') no-repeat top center / max(1180px, 100%) auto;
    border-radius: 15px;
    box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.08);
    &:last-child {
      margin-bottom: 0;
    }
    @include g.media_queries{
      padding-inline: 20px;
    }
    &__headFlex {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .modHeading {
        margin-bottom: 20px;
      }
    }
    &__content {
      margin-bottom: 64px;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  &Chart {
    width: fit-content;
    padding: 40px;
    background: var(--gradient_green);
    border-radius: 15px;
    > canvas {
      margin: auto;
    }
  }
  &Badge {
    &--unacquired {
      filter: grayscale(100%);
      opacity: 0.5;
    }
  }
}
