//文字コードはutf-8
@charset "UTF-8";

@use "../global" as g;

/* ボタン
========================================================*/
#{g.$prefix_mod}{
  &Btn{
    display: block;
    padding: 4px 24px;
    border-radius: 100px;
    font-weight: 700;
    text-align: center;
    white-space: nowrap;
    &--blue {
      background: var(--blue);
      color: var(--white);
    }
  }
}
