//文字コードはutf-8
@charset "UTF-8";

@use "../global" as g;

/* テーブル
========================================================*/
#{g.$prefix_mod}{
  &Table{
    width: 100%;
    margin: 0 0 40px;
    @include g.media_queries{
      margin: 60px 0 0;
    }
    td,th{
      padding: 32px 0px;
      @include g.fontsize(14);
    }
    th{
      font-weight: 700;
      vertical-align: top;
    }
    td{
    }
    @include g.media_queries{
      th{
        padding: 30px 20px 30px 0;
      }
      td{
      }
    }
    tr{
      border-top: 1px solid var(--gray);
      position: relative;
    }
  }
}
