//文字コードはutf-8
@charset "UTF-8";

@use "../global" as g;

/* AIチャット
========================================================*/
.aiChat {
  overflow: hidden;
  width: 100%;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.08);
  border-radius: 15px;
  position: relative;
  &.is-open {
    display: block;
    .aiChat {
      &__inner {
        max-height: 421px;
      }
      &__head {
        padding: 10px 10px 8px 12px;
        > .text {
          opacity: 1;
        }
        > .btn {
          &::before {
            transform: translate(-60%, -40%) scale(1);
          }
        }
      }
      &__body {
        opacity: 1;
        margin: 0 17px 45px 0;
        padding-inline: 23px 12px;
      }
      &__foot {
        padding-inline: 23px 28px;
        background: #d9d9d9;
        &__input {
          background: var(--white);
          
        }
      }
    }
  }
  &__inner {
    overflow: hidden;
    max-height: 0;
    transition: g.$transition_default;
  }
  &__head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: g.$transition_default;
    > .text {
      opacity: 0;
      display: flex;
      align-items: center;
      gap: 6px;
      font-weight: 700;
      @include g.fontsize(16);
      transition: g.$transition_default;
      &::before {
        content: "";
        display: block;
        width: 32px;
        height: 32px;
        aspect-ratio: 1;
        background: var(--gradient_green);;
        mask-image: url('/assets/img/common/ai_chat/icon_ai.svg');
        mask-size: contain;
        mask-repeat: no-repeat;
        mask-position: center;
      }
    }
    > .btn {
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 30px;
      height: 30px;
      aspect-ratio: 1;
      position: absolute;
      top: 11px;
      right: 10px;
      transform: rotate(-45deg);
      &::before,
      &::after {
        content: '';
        aspect-ratio: 1;
      }
      &::before {
        width: 8px;
        height: 8px;
        aspect-ratio: 1;
        border-top: 2px solid var(--white);
        border-right: 2px solid var(--white);
        transition: g.$transition_default;
        transform: scale(1, -1);
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-30%, -70%) scale(-1, -1);
      }
      &::after {
        width: 30px;
        height: 30px;
        background: var(--gradient_green);
        border-radius: 50%;
      }
    }
  }
  &__body {
    opacity: 0;
    overflow-y: scroll;
    height: 326px;
    transition: g.$transition_default;
    .chat {
      > p {
        width: 47.6%;
        padding: 27px 32px;
        @include g.fontsize(16);
      }
      &:not(:first-child) {
        margin-top: 18px;
      }
      &--user {
        > p {
          margin-left: auto;
          background: var(--light_blue);
          border-radius: 15px 15px 0 15px;
        }
      }
      &--bot {
        display: flex;
        gap: 16px;
        align-items: end;
        &::before {
          content: "";
          display: block;
          width: 40px;
          height: 40px;
          aspect-ratio: 1;
          background: var(--black);
          mask-image: url('/assets/img/common/ai_chat/icon_ai.svg');
          mask-size: contain;
          mask-repeat: no-repeat;
          mask-position: center;
        }
        > p {
          background: var(--mid_gray);
          border-radius: 15px 15px 15px 0;
          color: #000;
        }
      }
    }
  }
  &__foot {
    padding: 10px 62px 10px 12px;
    transition: g.$transition_default;
    .form {
      display: flex;
      align-items: center;
      gap: 8px;
      transition: g.$transition_default;
      &__input {
        resize: vertical;
        width: 100%;
        padding: 6px 14px;
        background: var(--mid_gray);
        border-radius: 10px;
        @include g.fontsize(16);
      }
    }
  }
}

